@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
@import url(https://fonts.googleapis.com/css?family=Gochi+Hand);
.heading__top {
  padding: .5rem 0 1.5rem 0;
}

.heading__bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-bottom: 1rem;
}

.heading__link {
  color: hsl(198, 1%, 29%);
  text-decoration: none;
}

.heading__link:hover {
  border-bottom: 3px solid hsla(166, 100%, 50%, 0.7);
}

.heading__title {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  padding: .2rem 1.2rem;
  background-color: hsla(166, 100%, 50%, 0.7);
  border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
}

.heading__logo {
  width: 24%;
}

input[type=checkbox] { display:none; } 
input[type=checkbox] + label:before {
  font-family: FontAwesome;
  display: inline-block;
  cursor: pointer;
  content: "\F096"; /* unchecked icon */
  letter-spacing: .8rem; /* space between checkbox and label */
}

input[type=checkbox]:checked + label:before { content: "\F046"; } /* checked icon */

.delete:before {
  font-family: FontAwesome;
  display: inline-block;
  cursor: pointer;
  content: "\F057";
  font-size: 1.5rem;
  color: hsla(6, 65%, 49%, .8);
}
.toDoList {
  text-align: left;
  padding: 5% 5%;
}
.form {
  margin: 12% 0 10% 0; 
}

.form__label {
  display: block;
  margin-bottom: 0.5rem;
}

.form__input {
  background-color: transparent;
  padding: 0.7rem;
  border: solid 3px transparent;
  border-bottom: dashed 3px #ea95e0;
  border-bottom-right-radius: 15px 3px;
  border-bottom-left-radius: 3px 15px;
  font-family: 'Gochi Hand', cursive;
  font-size: 1rem;
  color: hsla(260, 2%, 25%, 0.7);
}

.form__input:focus {
  outline: none;
  border: solid 3px #ea95e0;
}

.form__button {
  background-image: url('data:image/gif;base64,');
  background-color: hsla(166, 100%, 50%, 0.7);
  border: none;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  -webkit-transform-origin: center;
          transform-origin: center;
  font-family: 'Gochi Hand', cursive;
  text-decoration: none;
  padding-bottom: 3px;
  border-radius: 5px;
  box-shadow: 0 2px 0 hsl(198, 1%, 29%);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.form__button span {
  background: #f1f5f8;
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: 2px solid hsl(198, 1%, 29%);
}

.form__button:active, .form__button:focus {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  padding-bottom: 0px;
  outline: 0;
}
.link {
  text-decoration: none;
  margin-left: .3rem;
}
.lds-ring {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 64px;
  height: 64px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #ea95e0;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ea95e0 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #a39db2;
  min-height: 100vh;
  padding: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: hsl(198, 1%, 29%);
  font-family: 'Gochi Hand', cursive;
  text-align: center;
  font-size: 140%;
}

.container {
  width: 100%;
  height: auto;
  min-height: 500px;
  max-width: 500px;
  min-width: 500px;
  background: #f1f5f8;
  background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
  background-size: 25px 25px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
  padding: 1rem;
}
